*{
  font-family: 'zoho_puviregular';
  margin: 0px;
  padding: 0px;
}

.pageTitle{
  font-size: 30px;
  font-weight: 900;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #01033E;
  height: 10%;
  color: white;
  line-height: 40px;
}

body{
  background: linear-gradient(128.56deg, #00023D 21.51%, #1742D9 87.93%);
  background-attachment: fixed;
  height: 100vh;
}

#root {
  height: 100vh;
}

.App, .container {
  height: 90%
}

.leagueTitle{
  background: -webkit-linear-gradient(93.57deg, #FBD587 0%, #A37413 94.3%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 30px;
  font-weight: 900;
  text-align: center;
  padding: 2%;
  height: fit-content;
}

.matchContainer{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  height: fit-content;
}
.roundsBrackets{
  width: 65%;
  display: flex;
  flex-wrap: wrap;
  gap: 10%;
}
.qualifiers{
  width: 30%;
}

.team-group {
  width: 600px;
  color: #fff;
  padding: 1rem;
}

.qualified-team-group {
  width: 400px;
}

.team-group h2 {
  margin: 0;
}

.team-group table {
  border-collapse: separate;
  border-spacing: 0 10px;
  width: 100%;
  text-align: center;
}

.team-group table thead {
  font-weight: 600;
}

.team-group table tbody tr {
  background-color: #023047;
  position: relative;
}

.team-group table tbody :is(tr:nth-child(1), tr:nth-child(2)) {
  border-left: 5px solid white;
}

.team-group table :is(thead, tbody) tr :is(th:first-child, td:first-child) {
  text-align: left;
  padding-left: 2.5rem;
}

.team-group table tbody :is(tr:nth-child(1), tr:nth-child(2)) td:first-child, .team-group table tbody .qualified-team {
  border-left: 5px solid #35B433;
}

.team-group td {
  padding: 1rem 0;
}

.team-group table tbody td:not(:first-child, :last-child) {
  width: 30px;
}

.qualified-group-header {
  font-weight: 900;
  font-size: 30px;
  background: -webkit-linear-gradient(93.57deg, #FBD587 0%, #A37413 94.3%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.qualifiers_main{
  display: flex;
  height: 100vh;

  align-items: center;
  justify-content: center;
}
.qualifiersteam_header{
  color: #EBEBEB;
  font-size: 14px;
  margin-bottom: 10px;
  margin-left: 10px;
  display: block;
  font-family: 'zoho_puvisemibold';
}
.qualifiers_main .qualifiersteamone_partOne, .qualifiersteamone_parttwo, .qualifiersteamtwo_partOne, .qualifiersteamtwo_parttwo, .qualifiersteamthree_partOne, .qualifiersteamthree_parttwo{
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: 'zoho_puvisemibold';
  font-size: 16px;
}
.qualifiers_main .qualifiers_teamOne, .qualifiers_teamtwo, .qualifiers_teamthree{
  max-width: 350px;
  height: fit-content;
  border: 2px solid rgba(142, 202, 230, 0.88);

}
.qualifiers_main .qualifiers_teamOne span, .qualifiers_teamtwo span, .qualifiers_teamthree span{
  padding: 7px;
}
.qualifiers_main .qualifiersteamone_partOne{
  background: #023047;
}

.qualifiers_main .qualifiersteamone_parttwo{
  background: #023047;
  border-top:2px solid rgba(142, 202, 230, 0.88);
}
.qualifiers_main .qualifiersteamtwo_partOne{
  background: #023047;
}
.qualifiers_main .qualifiersteamtwo_parttwo{
  background: #023047;
  border-top:2px solid rgba(142, 202, 230, 0.88);
}
.qualifiers_main .qualifiersteamthree_partOne{
  background: #FFBA08;
}
.qualifiers_main .qualifiersteamthree_parttwo{
  background: #023047;
  border-top:2px solid rgba(142, 202, 230, 0.88);
}
.qualifiers_main .qualifiers_left_container{
  row-gap: 48px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.qualifiers_left_container::after{
  content: "";
  position: absolute;
  display: block;
  width: 70px;
  height: calc(100% - 105px);
  top: 65px;
  border: 2px solid #fff;
  border-left: none;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  right: -90px;
}
.qualifiers_right_container{
  margin-left: 185px;    
  position: relative;
}
.qualifiers_right_container::before{
  content: "";
  position: absolute;
  display: block;
  border: 1px solid #fff;
  width: 80px;
  top: 64%;
  left: -88px;
}
.qualifiers_main .qualifytime{
  background: #8ECAE6;
  min-width: 36px;
  text-align: center;
  color: #000;
}
.qualifiers_main .qualifiersteamone_partOne,.qualifiersteamone_parttwo, .qualifiersteamtwo_partOne, .qualifiersteamtwo_parttwo, .qualifiersteamthree_parttwo{
  color: #fff;
}
.flexremove{
  display: block;
}

.round{
  padding: 2%;
}
.match_left_container{
  row-gap: 48px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.leaugematch_left_container{

  display: flex;
  flex-direction: column;
  position: relative;
}

.qualifiers_main .winner{
  background: #FFBA08;
}

.round1{
  width: 30%;
}
.round2{
  width: 30%;
}
.roundTitle{
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-family: "zoho_puvibold";
  font-size: 30px;
}

.leagueBody{
  display: flex;
  flex-wrap: wrap;
  padding: 2%;
}
.standings{
  max-width: 40%;
  padding-left: 5%;
}

.leagueBody .matches{
  max-width: 60%;
  display: flex;
}






.qualifiers-main {
  width: 100vw;
  height: 675px;
  display: flex;
  width: 100vw;
  justify-content: flex-start;
  overflow-x: scroll;
  /* position: relative; */
}

.knockout-left-bracket {
  height: 100%;
  display: flex;
  gap: 1rem;
}

.knockout-right-bracket {
  height: 100%;
  display: flex;
  gap: 1rem;
  flex-direction: row-reverse;
}

.ro16-left-bracket, .ro16-right-bracket{
  padding: 1rem;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  gap: 5rem;
}

.quarters-left-bracket, .quarters-right-bracket {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 15rem;
  padding-top: 2rem;
}

.semis-left-bracket {
  display: flex;
  align-items: center;
  translate: -100px;
}

.semis-right-bracket {
  display: flex;
  align-items: center;
  translate: 100px;
}

.qualifiers-match {
  display: flex;
  flex-direction: column;
  color: #fff;
  position: relative;
}

.qualifiers-match-header {
  padding-bottom: 0.25rem;
}

.qualifiers-match :is(.qualifiers-team1, .qualifiers-team2) {
  display: flex;
  justify-content: space-between;
  width: 175px;
  background-color: #023047;
  align-items: center;
  padding: 0 0 0 1rem;
  border: 2px solid #8ECAE6;
}

.qualifiers-match .qualifytime {
  color: #023047;
  background-color: #8ECAE6;
  width: 30px;
  padding: 0.5rem;
  text-align: center;
}

.qualifiers-main .qualifiers-match-winner, .qualifiers-main .qualifiers-match-winner span {
  background-color: #FFBA08 !important;
  color: #023047;
}

.knockout-center {
  /* position: absolute;
  left: 50%;
  translate: -50%; */
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 15rem;
  align-items: center;
}

.finals {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.top-left-arrow::after, .quarters-top-left-arrow::after {
  position: absolute;
  right: 0;
  top: 60%;
  translate: 100%;
  content: "";
  width: calc(1rem + 100px);
  height: 100px;
  border: #fff solid;
  border-width: 0.25rem 0.25rem 0 0;
  border-top-right-radius: 1rem;  
}

.top-right-arrow::after, .quarters-top-right-arrow::after {
  position: absolute;
  left: 0;
  top: 60%;
  translate: -100%;
  content: "";
  width: calc(1rem + 100px);
  height: 100px;
  border: #fff solid;
  border-width: 0.25rem 0 0 0.25rem;
  border-top-left-radius: 1rem;  
}

.bottom-left-arrow::after, .quarters-bottom-left-arrow::after {
  position: absolute;
  right: 0;
  top: -40%;
  translate: 100%;
  content: "";
  width: calc(1rem + 100px);
  height: 100px;
  border: #fff solid;
  border-width: 0 0.25rem 0.25rem 0;
  border-bottom-right-radius: 1rem;
}

.bottom-right-arrow::after , .quarters-bottom-right-arrow::after{
  position: absolute;
  left: 0;
  top: -40%;
  translate: -100%;
  content: "";
  width: calc(1rem + 100px);
  height: 100px;
  border: #fff solid;
  border-width: 0 0 0.25rem 0.25rem;
  border-bottom-left-radius: 1rem;
}

.quarters-top-left-arrow::after, .quarters-bottom-left-arrow::after {
  width: 30px;
  height: 150px;
}

.quarters-bottom-left-arrow::after {
  top: -75%;
}

.quarters-top-right-arrow::after, .quarters-bottom-right-arrow::after {
  width: 30px;
  height: 150px;
}

.quarters-bottom-right-arrow::after {
  top: -75%;
}

.trophy {
  width: 125px;
  box-shadow: white;
  background: radial-gradient(46.74% 46.74% at 50.00% 50.00%, #FFF 0%, rgba(217, 217, 217, 0.00) 100%);
/* opacity: 0.6000000238418579; */
}